<template>
  <div class="new-task">
      <input v-model="name" @keydown.enter="add" type="text"
      class="form-element" placeholder="New task?"> 
      <button class="button-element" @click="add">+</button>
      
  </div>
</template>

<script>
export default {
    data(){
        return {
            name: ''
        }
    },
    methods: {
        add(){
            if(this.name != ''){
                this.$emit('taskAdded', { name: this.name })
                this.name = ''
            }
        }
    }
}
</script>

<style>
    .new-task {
        margin: 35px;
    }
    .form-element {
        outline: none;
        font-size: 2rem;
        border: 1px solid #fff;
        padding: 5px 10px 8px;
        color: #fff;
        width: 500px;
        background: #fff2;
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
    }
    .button-element {
        outline: none;
        font-size: 2rem;
        border: 1px solid #fff;
        padding: 5px 10px 8px;
        color: #fff;

        border-left: none;
        background-color: #2196f3;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
    }


</style>